body {
    background: #f6f6f6;
}

.header {
    z-index: 2;
    position: fixed;
    transition: .2s;
    top: 0;
    left: 0;
    right: 0;
    &__pinned {
        background: $white-color;
        box-shadow: 0 0 5px rgba($black-color, .2);
    }
}

.heeader-nav {
    &__ul {
        margin: 0;
        padding: 0;
        text-align: center;
    }
    &__li {
        list-style: none;
        display: inline-block;
        position: relative;
    }
    &__link {
        display: inline-block;
        position: relative;
        padding: 20px 30px;
        color: $black-color;
        &:hover {
            text-decoration: none;
        }
    }
}

.square {
    &__center {
        background: #e5f5ff;
        height: 100vh;
        max-height: 1000px;
        min-height: 800px;
        position: relative;
        overflow: hidden;
        &:after,
        &:before {
            left: 0;
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            background-repeat: repeat-x;
            background-position: center bottom;
        }
        &:before {
            bottom: -1px;
            background-image: url('../img/gress.png');
            z-index: 2;
            height: 200px;
        }
    }
    &__logo {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 20%;
    }
    &__info {
        position: absolute;
        left: 20%;
        top: 70%;
    }
    &__not-center {
        height: 50vh;
        max-height: 500px;
        background: $white-color;
        min-height: 400px;
        position: relative;
        display: block;
        overflow: hidden;
        &.active {
            .square__image {
                transform: scale(1.1);
                box-shadow: 0 0 20px rgba($black-color, .2);
            }
        }
    }
    &__image {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center bottom;
        position: absolute;
        top: 40%;
        margin-top: -100px;
        left: 50%;
        margin-left: -100px;
        height: 200px;
        width: 200px;
        border-radius: 50%;
        transition: 1.5s;
    }
    &__title {
        display: block;
        position: absolute;
        bottom: 0;
        height: 70px;
        left: 0;
        right: 0;
        text-align: center;
    }
    &__1 { background: #f3f8ff; }
    &__2 { background: #d2e6f4; }
    &__3 { background: #d7f3ff; }
    &__4 { background: #f0ffff; }
    &__link {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
}

#typed {
    font-size: 30px;
    font-family: 'Open Sans Condensed', sans-serif;
}

.typed-cursor {
    color: $black-color;
    font-size: 30px;
    font-weight: 700;
    font-family: 'Open Sans Condensed', sans-serif;
}

.accent {
    color: $green-color;
}

#cloud {
    left: 0;
    display: block;
    position: absolute;
    width: 100%;
    background-repeat: repeat-x;
    background-position: center bottom;
    top: 0;
    height: 250px;
    background-image: url('../img/cloud.png');
    background-size: cover;
    transition: .05s;
}

